<template>
  <div class="classManagement padding students">
    <!-- 班级管理 -->
    <!-- 弹框组件 -->
    <el-dialog
      :title="addClass"
      :visible.sync="dialogFormVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="班级类型：" :label-width="formLabelWidth" prop="classtype">
          <el-select v-model="form.classtype" placeholder="请选择班级类型">
            <el-option
              v-for="item in shuntTypeArr1"
              :key="item.code"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <span class="sss">所属学院：</span> -->
        <el-form-item label="所属学院：" :label-width="formLabelWidth" prop="academyId">
          <el-select v-model="form.academyId" placeholder="请选择所属学院" @change="changes1">
            <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
          <!-- <el-input placeholder="请输入内容" v-model="academyName" :disabled="true"></el-input> -->
        </el-form-item>
        <el-form-item label="所属专业：" :label-width="formLabelWidth" prop="specialtyId">
          <el-select v-model="form.specialtyId" placeholder="请选择所属专业">
            <el-option v-for="item in options1" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="班级名称：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off" placeholder="请输入班级名称"></el-input>
        </el-form-item>
        <el-form-item label="班级编码：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off" placeholder="请输入班级编码"></el-input>
        </el-form-item>

        <!-- <el-form-item label="年级：" :label-width="formLabelWidth" prop="grade">
                    <el-input v-model="form.grade" auto-complete="off"></el-input>
        </el-form-item>-->
        <el-form-item label="入学年级：" :label-width="formLabelWidth" prop="grade">
          <el-select v-model="form.grade" placeholder="请选择入学年级">
            <el-option v-for="item in options2" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('form')">取 消</el-button>
        <el-button type="primary" @click="seccons2('form')" v-show="flag">确 定</el-button>
        <el-button type="primary" @click="seccons22('form')" v-show="flag1">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 弹框组件  导入 -->
    <form :model="form" enctype="Content-Type: multipart/form-data">
      <el-dialog
        title="导入班级信息"
        :visible.sync="dialogFormVisible1"
        :show-close="true"
        :close-on-click-modal="false"
      >
        <div style="height: 350px;" class="steps-dv1">
          <el-steps :active="index" direction="vertical">
            <el-step title="上传文件" icon="el-icon-edit">
              <template slot="description">
                <div>
                  导入班级信息之前，请先“
                  <el-tooltip class="item" effect="dark" content="请先下载模板" placement="top-start">
                    <span @click="xiazai" class="span-dv">下载模板</span>
                  </el-tooltip>”，按照模板格式录入数据。
                </div>
              </template>
            </el-step>
            <el-step title="检查数据" icon="el-icon-upload" description="等待文件上传完毕后自动检查表格数据。">
              <template slot="description">
                <div>等待文件上传完毕后自动检查表格数据。</div>
              </template>
            </el-step>
            <el-step title="完成" icon="el-icon-picture"></el-step>
          </el-steps>
          <!-- <el-button @click="dialogFormVisible = false" type="primary" class="steps-btn">上传文件</el-button> -->
          <el-upload
            class="upload-demo steps-btn"
            ref="upload"
            :action="uploadUrl()"
            accept=".xls"
            :on-remove="handleRemove"
            :file-list="fileList"
            :auto-upload="true"
            :on-success="handleSuccess"
            :on-error="handleError"
            :before-remove="beforeRemove"
            :limit="1"
            :on-exceed="handleExceed"
          >
            <el-button slot="trigger" size="small" type="primary">上传</el-button>
            <div slot="tip" class="el-upload__tip">支持文件格式：.xls 类型的文件，且不超过20M</div>
          </el-upload>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closciick">取 消</el-button>
          <el-button type="primary" @click="submitUpload ">确 定</el-button>
        </div>
      </el-dialog>
    </form>
    <crumb style="padding: 0 5px;">
      <div class="new--btn" slot="right" @click="close">+ 新建</div>
      <div slot="right" @click="successdata" icon="el-icon-plus" style="float: left; margin-right: 10px; color: #03B187; cursor: pointer;"><SvgIcon icon-class="icondaoru" size="18px" style="float: left;margin: 32px 5px 0 0"></SvgIcon> 导入</div>
    </crumb>
    <div class="classManagement-tab">
      <div class="classManagement-inp">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <span class="ssss">班级类型：</span>
          <el-select v-model="valuearr" placeholder="请选择" class="pppp">
            <el-option
              v-for="item in shuntTypeArr"
              :key="item.code"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
          <span class="sssss">所属学院：</span>
          <el-select v-model="valuearr1" placeholder="请选择院校" class="pppp" @change="changes">
            <el-option
              v-for="item in optionsarr"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <span class="sssss">所属专业：</span>
          <el-select v-model="valuearr2" placeholder="请先选择院校" class="pppp">
            <el-option
              v-for="item in optionsarr1"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <span class="sssss">班级搜索：</span>
          <el-input v-model="formInline.user" class="pppp" placeholder="班级编码、班级名称、年级">
          </el-input>
          <div class="search--btn" style="margin: 6px 0 0 20px" @click="onSubmit">搜索</div>
        </el-form>
      </div>
        <el-table :data="tableData" stripe style="width: 100%" v-loading="loading">
          <el-table-column label="班级编码">
            <template slot-scope="scope">
              <span>{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="班级名称">
            <template slot-scope="scope">
              <span>{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属院校">
            <template slot-scope="scope">
              <span>{{ scope.row.academyVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属专业">
            <template slot-scope="scope">
              <span>{{ scope.row.specialtyVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="年级">
            <template slot-scope="scope">
              <span v-if="scope.row.grade">{{ scope.row.grade }}级</span>
              <span v-else>{{ scope.row.grade }}</span>
            </template>
          </el-table-column>
          <el-table-column label="班级类型">
            <template slot-scope="scope">
              <span>{{ scope.row.shuntType == 0 ? "未分流" : '已分流'}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="160px">
           <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>

<script>

export default {
  components: {},
  data() {
    let gradeYear = (rule, value, callback) => {
      let reg = /^[0-9]*\d{1,}$/;
      if (!reg.test(value)) {
        callback(new Error("年级必输输入数字类型"));
      } else {
        callback();
      }
    };
    return {
      shuntTypeArr: [
        {
          code: 0,
          value: "未分流"
        },
        {
          code: 1,
          value: "已分流"
        }
      ],
      shuntTypeArr1: [
          {
          code: 0,
          value: "未分流"
        },
        {
          code: 1,
          value: "已分流"
        },
        {
            code: "",
          value: "全部"
        }
      ],
      typeval: "",
      dialogFormVisible: false,
      dialogFormVisible1: false,
      formLabelWidth: "120px",
      flag: false,
      flag1: false,
      addClass: "",
      currentPage4: 1,
      total: 0,
      page: 1,
      pageSize: 15,
      pageNum: 1,
      form: {
        academyVal: "",
        specialtyVal: "",
        name: "",
        code: "",
        grade: "",
        specialtyId: "",
        academyId: "",
        classtype: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        optionsVal: "",
        optionsVal1: ""
      },
      formInline: {
        user: ""
      },
      rules: {
        classtype: [
          {
            required: true,
            message: "请选择班级类型",
            trigger: "blur"
          }
        ],
        academyId: [
          {
            required: true,
            message: "请选择所属学院",
            trigger: "blur"
          }
        ],
        specialtyId: [
          {
            required: true,
            message: "请选择所属专业",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            message: "请输入班级名称",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入班级编号",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            validator: gradeYear,
            trigger: "blur"
            // message: '年级为数字类型',
          }
        ]
      },
      tableData: [],
      loading: true,
      optionsarr: [],
      valuearr: "",
      optionsarr1: [],
      valuearr1: "",
      valuearr2: "",
      options: [],
      value: "",
      options1: [],
      value1: "",
      form1: {
        file: ""
      },
      file: "",
      index: 1,
      fileList: [],
      options2: [],
      academyName: ""
    };
  },
  methods: {
    //   导入
    successdata(index, row) {
      this.dialogFormVisible1 = true;
      this.teacherssId = row;
    },
    uploadUrl() {
      let token = localStorage.getItem("x_token");
      return `${this.API_URL}/basic/import/classInformation?x-token=${token}`;
    },
    xiazai() {
      location.href = this.API_URL + "/basic/import/template/classInformation";
    },
    submitUpload() {
      if (this.index === 3) {
        this.classlist();
        this.dialogFormVisible1 = false;
        this.index = 1;
      }
    },
    handleRemove(file, fileList) {
      this.form1.file = "";
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
      if (files.size > 20480) {
        this.$message.warning(`sorry 上传文件大于20M,请重新上`);
      }
    },
    closciick(file) {
      if (this.index === 2) {
        this.index = 1;
      }
      this.dialogFormVisible1 = false;
    },
    // 文件上传成功时的钩子
    handleSuccess(res, file, fileList) {
      console.log(res);
      if (res.status === 1) {
        this.$notify.success({
          title: "success",
          message: res.message
        });
        this.index = 3;
      } else {
        if (res.data == null) {
          this.$notify.warning({
            title: "warning",
            message: res.message
          });
        } else {
          this.$notify.warning({
            title: "warning",
            message: res.data[0].message
          });
        }
        this.index = 2;
        this.dialogFormVisible1 = true;
      }
    },
    // 文件上传失败时的钩子
    handleError(err, file, fileList) {
      this.$notify.error({
        title: "错误",
        message: `文件上传失败`
      });
    },
    beforeRemove(file, fileList) {
      if (this.index === 3) {
        this.index = 1;
      } else if (this.index === 2) {
        this.index = 1;
      }
      this.$confirm(`确定移除 ${file.name}？`);
    },
    shuntType() {
      this.$get("system/dict/field/shuntType").then(res => {
        this.shuntTypeArr1 = JSON.parse(JSON.stringify(res.data));
        this.shuntTypeArr = res.data;
        this.shuntTypeArr.unshift({
          code: "",
          value: "全部"
        });
      });
    },
    //  入学年级/basic/class/grades
    gradesClass() {
      this.$get("basic/class/grades").then(res => {
        if (res.status == 1) {
          this.options2 = res.data;
          this.options6 = res.data;
        }
      });
    },
    onSubmit() {
      let keywords = {
        shuntType: this.valuearr,
        academyId: this.valuearr1,
        specialtyId: this.valuearr2,
        keywords: this.formInline.user,
        page: this.pageNum,
        rows: this.pageSize
      };
      this.$post("/basic/class/list", keywords).then(res => {
        if (res.status == 1) {
          this.tableData = res.data;
          this.total = res.total;
          this.loading = false;
        }
      });
    },
    classlist() {
      let keywords = {
        page: this.pageNum,
        rows: this.pageSize
      };
      this.$post("/basic/class/list", keywords).then(res => {
        if (res.status == 1) {
          this.tableData = res.list;
          this.total = res.total;
          this.loading = false;
        }
      });
    },
    handleEdit(index, row) {
      //编辑对话框显示值
      this.addClass = "编辑班级";
      this.flag1 = true;
      this.flag = false;
      this.dialogFormVisible = true;
      this.$get(`/basic/specialty/all/${row.academyId}`).then(res => {
        if (res.status == 1) {
          this.options1 = res.data;
        }
      });
      this.form.name = row.name;
      this.form.grade = row.grade;
      this.form.code = row.code;
      this.form.id = row.id;
      this.form.academyId = row.academyId;
      this.form.specialtyId = row.specialtyId;
      this.form.classtype = row.shuntType;
    },
    // 添加班级    /basic/class/add
    addcalss() {
      let form = {
        code: this.form.code,
        name: this.form.name,
        grade: this.form.grade,
        academyId: this.form.academyId,
        specialtyId: this.form.specialtyId,
        shuntType: this.form.classtype
      };
      this.$post("/basic/class/add", form).then(res => {
        if (res.status == 1) {
          this.$notify({
            message: "新增成功",
            type: "success"
          });
          this.tableData.unshift(res.data);
          this.classlist();
        } else if (res.status == 0) {
          this.$notify.warning({
            message: res.message,
            type: "warning"
          });
        }
      });
    },
    // /basic/academy/all 所有学院
    allxue() {
      this.$get(`/basic/academy/all?type=${1}`).then(res => {
        if (res.status == 1) {
          // this.academyOption1 = Object.assign([], res.data)
          // this.form.academyId = this.academyOption1[0].id
          // this.academyName= this.academyOption1[0].name
          this.options = JSON.parse(JSON.stringify(res.data));
          this.optionsarr = res.data;
          this.optionsarr.unshift({
            code: "",
            name: "全部"
          });
        }
      });
    },
    changes() {
      let id = this.valuearr1;
      if (id) {
        this.$get(`/basic/specialty/all/${id}`).then(res => {
          if (res.status == 1) {
            this.optionsarr1 = res.data;
            this.optionsarr1.unshift({
              code: "",
              name: "全部"
            });
          }
        });
      } else {
        this.optionsarr1 = [];
      }
    },
    changes1() {
      let id = this.form.academyId;
      this.$get(`/basic/specialty/all/${id}`).then(res => {
        if (res.status == 1) {
          this.options1 = res.data;
        }
      });
    },
    seccons2(form) {
      // 确定按钮的点击tianjia编辑内容
      this.$refs[form].validate(valid => {
        if (valid) {
          this.addcalss();
          this.dialogFormVisible = false;
          this.classlist();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialogFormVisible = false;
      this.form = {};
    },
    handleClose() {
      this.dialogFormVisible = false;
      this.$refs["form"].resetFields();
      this.form = {};
    },
    seccons22(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          // 确定按钮的点击提交编辑内容 /basic/class/edit
          let name = this.form.name;
          let grade = this.form.grade;
          let code = this.form.code;
          let id = this.form.id;
          let academyId = this.form.academyId;
          let specialtyId = this.form.specialtyId;
          let shuntType = this.form.classtype.toString();
          this.$put("/basic/class/edit", {
            name,
            grade,
            code,
            id,
            academyId,
            specialtyId,
            shuntType
          }).then(res => {
            if (res.status == 1) {
              // this.form.name = res.data.name
              // this.form.grade = res.data.grade
              // this.form.id = res.data.id
              // this.form.academyId = res.data.specialtyId
              // this.form.specialtyId = res.data.academyId
              // this.form.classtype = res.data.shuntType
              this.$notify.success({
                title: "success",
                message: "编辑成功"
              });
              this.$refs[form].resetFields();
              this.dialogFormVisible = false;
              this.onSubmit();
            } else {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    close() {
      this.addClass = "添加班级";
      this.flag = true;
      this.flag1 = false;
      this.dialogFormVisible = true;
      this.form = {};
      this.value = "";
      this.value1 = "";
    },
    handleDelete(index, row) {
      // 删除班级信息   /basic/class/del/{id}
      this.$confirm("确定删除该信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$deletes(`/basic/class/del/${row.id}`).then(res => {
            if (res.status == 1) {
              this.onSubmit();
            } else if (res.status == 0) {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
              this.onSubmit();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.onSubmit();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.onSubmit();
    }
  },
  mounted() {
    this.allxue();
    this.classlist();
    //this.shuntType();
    this.gradesClass();
  }
};
</script>

<style lang="scss">
.classManagement {
  margin-bottom: 40px;
  .el-select {
    display: -webkit-box;
    // margin-left: 119px;
    // margin-bottom: 12px;
  }

  .ssss {
    float: left;
    line-height: 50px;
    font-weight:bold;
    color: #696969;
    font-size: 14px;
    margin-right: 11px;
  }

  .sss {
    line-height: 50px;
    font-size: 14px;
    margin-left: 37px;
  }

  .sssss {
    float: left;
    margin-left: 20px;
    line-height: 50px;
    font-weight:bold;
    color: #696969;
    font-size: 14px;
    margin-top: 2px;
    margin-right: 11px;
  }

  .classManagement-tab {
    border-radius: 10px 10px 0 0;
  }

  .classManagement-inp {
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    width: 100%;
    overflow: hidden;
    margin-bottom: 20px;
    box-sizing: border-box;
    .pppp {
      width: 12.2%;
      float: left;
      margin: 0;
      margin-top: 6px;
    }

    .classManagement-btn {
      margin-top: 7px;
      float: right;

      p {
        float: left;
        margin-right: 20px;
        margin-top: 4px;
        cursor: pointer;
        color: rgb(95, 119, 255);
        font-size: 14px;

        img {
          float: left;
          margin-top: 2px;
          margin-right: 4px;
        }
      }
    }
  }

  .el-form--inline .el-form-item {
    margin-left: 20px;
  }

  .block {
    // float: left;
    width: 100%;
    text-align: right;
    background-color: #fff;
    padding: 12px 0;
    border-radius: 0 0 10px 10px;
    padding: 20px;
    box-sizing: border-box;
    margin-bottom: 100px;
  }

  .steps-dv1 {
    position: relative;

    .steps-btn {
      position: absolute;
      top: 66px;
      left: 54px;
    }
  }

  .span-dv {
    cursor: pointer;
    color: #000;
    padding: 0 8px;
  }
}
</style>



